@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Montserrat', sans-serif;
  color:  #CBC9CF;
  background-color: #46424D;
  width: 100%;
  height: 100vh;
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 9% auto;
  grid-template-areas:
  "h h h"
  "l l r";
}

h2 {
  margin-left: 5%;

}



header {
  grid-area: h;
  display: flex;
  justify-content: center;
  background-color: #46424D;
  align-items: center;
  font-size: 1.5em;
  text-transform: uppercase;
}
.form-timer-container {
  grid-area: l;
  background-color: #2B2733;
}

.form-inner-container {
  background-color: #46424D;
  border-radius: 5px;
  box-shadow: 3px 3px #26222D;
  margin: 0 5%;
  height: 85%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 0 8%
}

.menu-buttons {
  display: flex;
  justify-content: space-between;
}

.menu-buttons button {
  width: 27%;
  height: 4em;
  border: 2px solid lightgray;
  background-color: #46424D;
  font-size: 1.2em;
  color:  #CBC9CF;
  outline: none;
  text-align: center;
  border-radius: 5px;
}

.menu-buttons img {
  height: 30px;
  width: 30px;
  display: block;
  text-align: center;
  margin: auto;
  padding-bottom: 2px;
  pointer-events: none;
}

.activity-description {
  margin-top: 10%;
  font-size: 1.3em;
  display: flex;
  flex-direction: column;
}

.activity-description input,
.min-sec-inputs input {
  background-color: #46424D;
  outline: none;
  margin-top: 2%;
  border: none;
  border-bottom: 2px solid lightgray;
  font-size: 1.2em;
  color: #CBC9CF;
}

.minutes-seconds {
  display: flex;
  justify-content: space-between;
  margin-top: 8%;
}

.minutes-seconds-error {
  display: flex;
  justify-content: space-between;
  margin-top: 1%;
}

.min-sec-inputs {
  display: flex;
  flex-direction: column;
  width: 45%;
  font-size: 1.3em;
}

.start-activity-btn {
  margin-top: 8%;
  display: flex;
  justify-content: flex-end;
  text-transform: uppercase;
  height: 6%;
}
.start-activity-btn-error {
  margin-top: 1%;
  display: flex;
  justify-content: flex-end;
  text-transform: uppercase;
  height: 6%;
}


.start-activity {
  border-radius: 5px;
  width: 30%;
  text-transform: uppercase;
  padding: 1%;
  outline: none;
  font-size: 1.0em;
}

.min-error img,
.desc-error img {
  display: inline-block;
  height: 15px;
  width: 15px;
  margin-right: 2%;
}

.min-error,
.desc-error {
  font-size: .8em;
  align-items: center;
  color: #EFB7EC
}

.menu-buttons .study-outline {
  border: 2px solid #B3FD78;
  color: #B3FD78;
}

.menu-buttons .meditate-outline {
  border: 2px solid #C278FD;
  color :#C278FD;
}

.menu-buttons .exercise-outline {
  border: 2px solid #FD8078;
  color: #FD8078;
}

.timer-inner-container {
  position: relative;
  background-color: #46424D;
  border-radius: 5px;
  box-shadow: 3px 3px #26222D;
  margin: 0 5%;
  height: 85%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0 8%
}

.timer-column-box {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 45%;
  height: 80%;
  /* border: 1px solid yellow; */
}

.top-timer-column-box {
  margin-top: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1.2em
}

.top-timer-column-box p {
  margin: 0;
  font-weight: 700;
}

.timer-display {
  font-family: sans-serif;
  font-size: 2.5em;
  color: white;
}

.timer-circle-btn {
  font-size: 1.5em;
  margin-top: 2em;
  background-color: #46424D;
  color: lightgray;
  height: 50%;
  width: 80%;
  border-radius: 50%;
  display: block;
  outline: none;
  text-transform: uppercase;
}

.study-outline {
  border: 3px solid #B3FD78;
}

.study-header {
  color: #B3FD78
}

 .meditate-outline {
  border: 3px solid #C278FD;
}

.meditate-header {
  color: #C278FD
}

 .exercise-outline {
  border: 3px solid #FD8078;
}

.exercise-header {
  color: #FD8078
}

.log-activity-btn {
  font-size: 1.0em;
  width: 30%;
  height: 6%;
  margin-bottom: 7%;
  text-transform: uppercase;
  border-radius: 4px;
  outline: none;
}

.new-activity-btn {
  font-size: 1.0em;
  width: 50%;
  height: 9%;
  margin-top: 10%;
  text-transform: uppercase;
  border-radius: 4px;
  outline: none;
}

.replay-img {
  position: absolute;
  height: 30px;
  width: 30px;
  top: 10px;
  right: 10px;
}

.activities-container {
  grid-area: r;
  background-color: #2b2733;
}

.no-activity-msg {
  margin-left: 5%;
  text-align: center;
}

.activities-inner-container {
  display: flex;
  flex-direction: column;
  margin: 0 5%;
}

.activity-section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.activity-section-header img {
  height: 24px;
  width: 24px;
  margin-right: .7em;
}

.activity-icons {
  margin-right: 5%;
}

/* .activity-icons:hover {
  cursor: pointer;
} */

.activity-icons img {
  cursor: pointer
}



.activity-card-container {
  margin-bottom: 3%;
  background-color: #46424D;
  border-radius: 5px;
  box-shadow: 3px 3px #26222D;
  padding-left: 2%;
}

.activity-card-subcontainer {
  height: 4.5em;
  display: flex;
  flex-direction: column;
  align-items: left;
  margin-right: 2%;
  margin-top: 2%;
}

.ac-top {
  height: 45%;
  display: flex;
  align-items: center;
  margin-bottom: 1%
}
.activity-min-sec {
  text-transform: uppercase;
  padding: 0 .5em
}

.activity-exercise {
  color: #FD8078;
  font-weight: bold;
}

.activity-meditate {
  color :#C278FD;
  font-weight: bold;
}

.activity-study {
  font-weight: bold;
  color: #B3FD78;
}

.ac-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50%
}


/* .ac-bottom img {
  height: 21px;
  width: 25px;
  padding-left: 5px;
  padding-top: 5px
} */

.ac-bottom .heart {
  height: 24px;
  width: 22px;
  padding-left: 5px;
  /* margin-top: 5px; */
}

.ac-bottom .delete-img {
  height: 23px;
  width: 18px;
  padding-left: 5px
}

.ac-bottom .replay-image {
    height: 23px;
    width: 18px;
    padding-left: 5px
}

.activity-btns {
  padding-right: 9px
}

.activity-btns img {
  cursor: pointer;
}

.exercise {
  border-right: 5px solid #FD8078
}

.meditate {
  border-right: 5px solid #C278FD
}

.study {
  border-right: 5px solid #B3FD78
}
